//--------------------------------------------------------------------
// archive (tile style)
//--------------------------------------------------------------------
.archive-tiles {
  .term-description {
    padding: 20px;
    margin-bottom: 30px;
    border: 1px solid $base-gray-6;
    p {
      font-size: 1.5rem;
      line-height: 1.75;
    }
  }
  .archive-header {
    margin-bottom: 25px;
    .page-title {
      font-size: 2.8rem;
      line-height: 1.3;
      @include font-weight(normal);
    }
  }
  .entry-articles {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    font-size: 1.5rem;
    margin: 0 -20px;
  }
  .entry-article {
    width: calc(100% / 3);
    padding: 0 20px;
    margin-bottom: 40px;
    box-sizing: border-box;
    #one-column & {
      width: calc(100% / 4);
    }
  }
  .thumbnail {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    margin: 0 auto 15px;
    background-color: #fff;
    overflow: hidden;
    a {
      display: block;
      @include opacity-transition;
      &:hover {
        @include opacity-hover;
      }
    }
    img {
      width: 100%;
      height: auto;
      max-height: 100%;
    }
  }
  .no-thumbnail {
    position: relative;
    a {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: $base-gray-7;
      span {
        position: absolute;
        top: 50%;
        right: 0;
        left: 0;
        color: $base-gray-5;
        font-size: 1.2rem;
        line-height: 1;
        text-align: center;
        margin-top: -.5em;
      }
    }
  }
  .entry-header {
    margin-bottom: 10px;
    .title {
      font-size: 2rem;
      line-height: 1.3;
      @include font-weight(normal);
      margin-bottom: 5px;
    }
    .meta {
      line-height: 0;
      li {
        display: inline-block;
        color: $base-gray-2;
        font-size: 1.3rem;
        line-height: 1.4;
        margin: 0 20px 2px 0;
      }
      .year,
      .month,
      .day {
        margin-right: 3px;
      }
    }
  }
  .entry-body {
    text-justify: inter-ideograph;
    text-align: justify;
    p {
      font-size: 1.5rem;
      line-height: 1.6;
    }
  }
  #no-result {
    clear: both;
    text-align: center;
    padding: 60px 0 80px;
    p {
      color: $base-gray-3;
      font-size: 1.6rem;
    }
  }
}
